<template>
    <div class="login-box">
        <header>
            <div class="logo">
                <img src="./../assets/logo.png" alt="">
                <span>超对称运营后台</span>
            </div>
            <div class="sign-out">
                <img src="./../assets/exit.png" alt="">
                <span>退出</span>
            </div>
        </header>
        <div class="bg-color"></div>
        <div class="login-account">
            <h2>超对称后台登陆系统</h2>
            <div>
                <input type="text" placeholder="用户名" v-model="userName">
                <input type="password" placeholder="密码" v-model="passWord">
            </div>
            <div class="button" @click="login">
                登录                
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios'
import { setStorge } from '@/util/localstorage'
import { javaServiceUrl } from './../util/constant.js'

export default {
    data() {
        return {
            userName: '',
            passWord: '',
        }
    },
    methods: {
        login() {
            Axios({
               url:javaServiceUrl + '/userPermission/login',
               method: 'get', 
               params:{
                    username:this.userName,
                    password:this.passWord,
               }
            }).then(data=>{
                if(!data.data) {
                    this.$alert("用户名或者密码错误")
                } else {
                    setStorge('userName', data.data.username);
                    setStorge('userId', data.data.id);
                    setStorge('token', data.data.token);
                    this.$router.push('/userMnagement')
                }
            })
        }
    },
    mounted() {

    }
}
</script>
<style lang="scss" scoped>
.login-box{
    header{
            height: 70px;
            width: 100%;
            line-height: 70px;
            background: rgb(96,152,226);
            color: #fff;
            display: flex;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 999;
            justify-content: space-between;
            .logo{
                margin-left: 20px;
            }
            .sign-out{
                margin-right: 30px;
            }
            img{
                vertical-align: middle;
                margin-right: 10px;
            }
        }
    .bg-color{
        width: 100%;
        height: 300px;
        background-image: url("./../assets/background.png");
    }
    .login-account{
        width: 400px;
        height: 300px;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -150px;
        margin-left: -200px;
        z-index: 999;
        h2{
            text-align: center;
            margin: 20px 0;
        }
        input{
            width: 300px;
            height: 40px;
            border: 1px solid #f2f2f2;
            margin: 10px 50px;
        }
        .button{
            width: 300px;
            height: 40px;
            background: #409EFF;
            margin: 10px auto;
            line-height: 40px;
            text-align: center;
            color:#fff;
            border-radius: 6px;
            cursor: pointer;
        }
    }
}

</style>



